.logo {
  height: 10%;
  width: 10%;
}

.h3 {
  margin-top: 2.5%;
  margin-left: 5%;
  font-style: italic;
  font-weight: bold;
  font-size: 100%;
}

.box {
  margin-left: 5%;
  width: 10%;
}

#textarea {
  height: 30%;
  width: 40%;
}

#addbook {
  margin-left: 5%;
}

h1 {
  font-family: "Times New Roman";
  font-style: italic;
  margin-left: 30%;
  margin-top: 4%;
}

nav {
  list-style-type: none;
  margin: 0%;
  padding: 0%;
  overflow: hidden;
  background-color: #333;
}

nav a {
  display: block;
  color: white;
  text-align: center;
  padding: 1.5% 3.72%;
  text-decoration: none;
  float: left;
}

nav a:hover:not(.active) {
  background-color: #111;
}

.active {
  background-color: #333;
}

.head {
  display: flex;
  flex-wrap: wrap;
  margin-left: 2%;
}

.search {
  margin-top: 2.5%;
  margin-left: 5%;
  font-style: italic;
  font-weight: bold;
  font-size: 100%;
}

.submit {
  background-color: #333;
  border: none;
  color: white;
  padding: 5px 7px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}

.box {
  padding: 7px 60px;
}

.list {
  padding: 3%;
  /* background: gray; */
}

.img-home {
  height: 100%;
  width: 100%;
}

.img-books {
  height: 50%;
  width: 50%;
  padding: 2%;
}

.books {
  height: 50%;
  width: 50%;
  /* display: flex;
  flex-wrap: wrap;  */
  float: right;
  padding-top: 5%;
}

.best {
  padding: 2%;
}

button,
.button {
  background-color: #333;
  border: none;
  color: white;
  padding: 5px 7px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}

/* ======================================================================================================================================= */

@media (max-width: 1000px) {
  .logo {
    height: 20%;
    width: 20%;
  }

  h1 {
    font-family: "Times New Roman";
    font-style: italic;
    margin-left: 10%;
    margin-top: 3%;
  }

  nav {
    list-style-type: none;
    margin: 0%;
    padding: 0%;
    /* overflow: hidden; */
    background-color: #333;
  }

  nav a {
    display: block;
    color: white;
    /* text-align: center; */
    padding: 5% 25% 5% 25%;
    text-decoration: none;
    /* float: right; */
    margin-left: 10%;
  }

  nav a:hover:not(.active) {
    background-color: #111;
  }

  .active {
    background-color: #333;
  }

  .books {
    padding: 5%;
  }

  .img-books {
    height: 50%;
    width: 50%;
    padding: 2%;
    padding-bottom: 20%;
  }
  /* 
  .books {
     height: 50%;
    width: 50%;
     display: flex;
    flex-wrap: wrap;  
     float: right;
    padding-top: 5%; 
  } */
}
